	module.exports = {"data":{"markdownRemark":{"html":"<figure style=\"width: 400px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/hk-17ace195dcfc9c0bfa37431b514bdbac-13c16.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 652px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"hk\" title=\"\" src=\"/static/hk-17ace195dcfc9c0bfa37431b514bdbac-13c16.png\" srcset=\"/static/hk-17ace195dcfc9c0bfa37431b514bdbac-ee3f5.png 300w,\n/static/hk-17ace195dcfc9c0bfa37431b514bdbac-eece9.png 600w,\n/static/hk-17ace195dcfc9c0bfa37431b514bdbac-13c16.png 652w\" sizes=\"(max-width: 652px) 100vw, 652px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption> </figcaption>\n</figure>\n<h2 id=\"span-stylecolorbrown-mùa-nào-thích-hợp-để-du-lịch-hong-kong-span\"><a href=\"#span-stylecolorbrown-m%C3%B9a-n%C3%A0o-th%C3%ADch-h%E1%BB%A3p-%C4%91%E1%BB%83-du-l%E1%BB%8Bch-hong-kong-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:brown\"> Mùa nào thích hợp để du lịch Hong Kong? </span></h2>\n<p>Mình đến Hong Kong là vào tháng 5, lúc này đang là Mid-season sale, các mặt hàng thường được giảm giá 30%. Bạn nào muốn canh dịp Final sale thì đi vào tháng 1, 7, 8 và 12 nhé. Nhưng mình nghĩ đi tháng 12 là đẹp vì lúc đó là lễ Giáng sinh, thời tiết dễ chịu nữa. Tháng 7, 8 hay có mưa lắm. Trước khi đi cũng nên xem dự báo thời tiết để mang đồ thích hợp nhé.</p>\n<h2 id=\"span-stylecolorslateblue-Địa-điểm-thu-hút-ở-hong-kong-span\"><a href=\"#span-stylecolorslateblue-%C4%90%E1%BB%8Ba-%C4%91i%E1%BB%83m-thu-h%C3%BAt-%E1%BB%9F-hong-kong-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:slateblue\"> Địa điểm thu hút ở Hong Kong </span></h2>\n<p>Mình đã đi chơi ở những địa điểm này:Disneyland, Ocean Park, Wong Tai Sin, Chi Lin - Nan Lian Garden, Peak Tram - The Peak, Tsim Sha Tsui Clock Tower, Avenue of Stars, Tsim Sha Tsui, Tai O Fishing Village, Ngong Ping, Citigate Tung Chung Outlet, Temple Market, Jade Market, Mid-levels Escalator (thang cuốn ngoài trời dài nhất trên thế giới), HK3D Museum (lúc mình đi thì ở đó đang có triển lãm One Piece 3D).</p>\n<p>Bài viết chi tiết về những địa điểm này ở <a href=\"http://aquabubu.com/blog/travel/Hong-Kong-trip/\" target=\"_blank\">đây</a> nhé.</p>\n<h2 id=\"span-stylecolortomato-Ẩm-thực-hong-kong-span\"><a href=\"#span-stylecolortomato-%E1%BA%A8m-th%E1%BB%B1c-hong-kong-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:tomato\"> Ẩm thực Hong Kong </span></h2>\n<p>Có một website Hong Kong là <a href=\"https://www.openrice.com/en/hongkong\" target=\"_blank\">OpenRice</a>, web này có tất tần tật các thông tin về nhà hàng, món ăn, ảnh, giá tiển, thời gian mở cửa và hướng dẫn đến đó luôn nhé, khá giống foody của Việt Nam. Còn <a href=\"http://aquabubu.com/blog/cuisine/Hong-Kong-food-trip/\" target=\"_blank\">đây</a> là bài viết của mình về những món ăn mình đã thử khi đi Hong Kong.</p>\n<p>Ẩm thực Hồng Kông là sự kết hợp giữa ẩm thực Trung Quốc và phương Tây, rất ngon, rất đáng nhớ. Đây là điều làm mình ấn tượng nhất khi đi du lịch nước này. Mình đã ăn ở khoảng 20 quán nổi tiếng và những nhà hàng được bình chọn bởi người dân địa phương thì rất là ngon nhé.</p>\n<p>Người dân ở đây ăn nhiều thịt ít rau. Khi bạn ăn mì, nếu muốn ăn thêm rau, bạn phải gọi làm thêm đấy. Bạn sẽ bị tính thêm tiền, nhưng cái phần rau gọi thêm cũng đủ lớn cho cả hai người ăn rồi nhé.</p>\n<h2 id=\"span-stylecolorseagreen-phương-tiện-đi-lại-span\"><a href=\"#span-stylecolorseagreen-ph%C6%B0%C6%A1ng-ti%E1%BB%87n-%C4%91i-l%E1%BA%A1i-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:seagreen\"> Phương tiện đi lại </span></h2>\n<p>Mình chủ yếu đi bằng MTR. Cũng có những phương tiện đặc biệt để thử như xe buýt điện chạy trên đường sắt Ding Ding, tàu du lịch qua đảo Star Perry (hai loại này chủ yếu để vui chơi và thư giãn, giá rất rẻ, ~9000VND).</p>\n<h2 id=\"span-stylecolorcrimson-fee-span\"><a href=\"#span-stylecolorcrimson-fee-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:crimson\"> Fee </span></h2>\n<p>Danh sách dưới đây là <strong>khoản tiền cần chi</strong> cho chuyến đi này. Tất nhiên, mỗi người mỗi khác, nhưng bạn có thể xem tham khảo để ước lượng cho chuyến đi của mình. Và đây là chi phí <strong>chỉ dành cho 1 người</strong>..</p>\n<p><strong>Vé máy bay</strong>: cái này tùy hãng bay nên mình không đưa ra số tiền nhất định nhé, nhưng có thể canh vé rẻ của Vietnam Airline, tầm 2tr363/người/khứ hồi (đã tính thuế &#x26; phí).</p>\n<p><strong>Visa</strong>: Thông thường chi phí xin visa đi Hong Kong mất khoảng ~$55/ người</p>\n<p><strong>Hotel</strong>: ~2.800.000 VND (4 đêm ở khách sạn 4 sao)</p>\n<p><strong>Disneyland</strong>: ~1.400.000 VND</p>\n<p><strong>Ocean Park</strong>: ~700.000VND</p>\n<p><strong>Ngong Ping</strong>: ~500.000 VND</p>\n<p><strong>Vé vào bảo tàng 3D</strong>: ~450.000 VND</p>\n<p><strong>Phí đi lại</strong> (MTR-xe buýt-tàu-peak tram...): ~1.300.000 VND</p>\n<p><strong>Ăn uống, quà cáp, chi tiêu linh tinh</strong>: tùy vào việc mua nhiều hay ít nhé, mình dùng khoảng 3.000.000 VND</p>","timeToRead":3,"excerpt":" Mùa nào thích hợp để du lịch Hong Kong?  Mình đến Hong Kong là vào tháng 5, lúc này đang là Mid-season sale, các mặt hàng thường được giảm…","frontmatter":{"title":"Kinh nghiệm du lịch Hong Kong","cover":"/images/hk.png","date":"2015/05/15","category":"Travel","tags":["Travel","hongkong-tips","travel-exspense","kinh-nghiem","hong-kong","chi-phi","an-uong","what-to-bring","chuan-bi-do"]},"fields":{"slug":"/vi/Hong-Kong-traveling-tips/"}}},"pathContext":{"path":"/vi/Hong-Kong-traveling-tips/","langKey":"vi"}}

